import { Component, OnInit, ComponentFactoryResolver, ViewContainerRef, ViewChild } from '@angular/core';
import { JsonService } from '../../../json.service';
import { TableComponent } from '../../../table/table.component';
import { ChangeDetectionStrategy } from '@angular/compiler/src/core';
import { Observable } from 'rxjs';
import { of } from 'rxjs';


@Component({
  selector: 'app-kardex',
  templateUrl: './kardex.page.html',
  styleUrls: ['./kardex.page.scss'],
  /* changeDetection: ChangeDetectionStrategy.OnPush */
})
export class KardexPage implements OnInit {
 /*  @Input() public page: string;
  @Input() public init: number; */
  @ViewChild('container', {read: ViewContainerRef}) container: ViewContainerRef;
  sucursales: any [];
  tableComponent = TableComponent;
  table: Observable<any[]>;
  tableTitle: any [];
  records: boolean;

constructor(public jsonService: JsonService,
            private componentFactoryResolver: ComponentFactoryResolver, private viewContainerRef: ViewContainerRef) {
  this.getSucursales();
}
ngOnInit() { this.tableResults(null, null, null); }

getSucursales() {
    this.jsonService.getJson('https://app.networkingsas.com/app/sucursal.php').subscribe((res: Array<string>) => {
      console.log(res);
      let data = [];
      let i = 0 ;
      // tslint:disable-next-line: only-arrow-functions
      Object.keys(res).forEach(function(key) {
        data[i] = (res[key]);
        i++;
      });
      console.log(data.length);
      // tslint:disable-next-line: no-string-literal
      this.sucursales = data;
     });
  }
  tableResults( idsucursal: string, periodo: string , filtro: string ) {
    const formData = new FormData();
    formData.append('idsucursal', idsucursal);
    formData.append('periodo', '');
    formData.append('filtro', '');
    this.jsonService.postJson('https://app.networkingsas.com/app/inventario/kardex.php', formData).subscribe
    ((res: Array<string>) => {
      console.log(res);
      if (!res){
        this.records= false;
        return;
      }
      this.records=true
      // tslint:disable-next-line: prefer-const
      let data = [];
      let i = 0 ;
      // tslint:disable-next-line: only-arrow-functions
      Object.keys(res).forEach(function(key) {
        /* console.log(res[key]); */
        data[i] = (res[key]);
        i++;
      });
      console.log(data.length);
      // tslint:disable-next-line: no-string-literal
      this.tableTitle = data[0];
      this.table = of(data);
      /* this.tableTitle= of(this.tableTitle); */
      /* window.location.reload(); */
     /*  alert(data.length); */
     });
  }
}
